import { mapState, mapMutations } from 'vuex';
import uniPagination from "@/components/uni-pagination/uni-pagination.vue"
import http from '../../common/js/request.js';
import uniBadge from "@dcloudio/uni-ui/lib/uni-badge/uni-badge.vue"

export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName', 'serverUrl', 'user']),
	data() {
		return {
			navlist: [
				'已提交','待同意', '进行中', '待支付','已完成'
			],
			currentIndex: 0,
			pages: 1,
			dataList:[],
			page:{
				pageNum:1,
				pageSize:10,
				total:'',
				pageTotal:'0'
			},
			order_list: [
					
			
			],
			classNum:{
				'0':'',
				'1':'',
				'2':'',
				'3':'',
				'4':'',
				'5':''
			},
			searchEntity:{
				status:1
			},
			status: '',	
			phoneStatus:{}
		}
	},
	components:{uniPagination,uniBadge},
	methods: {
		navselect(index){
			if(index==0){
				this.searchEntity.status=1;
			}
			else if(index==1){
				this.searchEntity.status=0;
			}else{
				this.searchEntity.status=index;
			}
			this.currentIndex=index;
			this.getDataList();
			
			
			
		},
		goDetail(v){
			console.log(v)
			
		},
		paginat(v){
			
			this.page.pageNum=v;
			console.log(v)
			
		},
		goDetail(o){
			
			uni.navigateTo({
				url:'../cargo/cargo/cargo?id='+o.id
			})
		},
		getDataList(){
			
			let url='/user/userPoint/findManyWithPage';
			let data={
			        pageNum:this.page.pageNum,
					pageSize:this.page.pageSize,
					status:this.searchEntity.status,
					userId:this.user.id
			    }
			 let entity={};
			 entity.url=url;
			 entity.data=data;
			 const that=this;
		     http.get(entity).then(
		               res => {
						  
						  that.page.pageTotal=res.pageCount;
						  that.order_list=[];
						  res.list.forEach(p=>{
							  
							  p.cargoImage=JSON.parse(p.cargoImage);
							  that.order_list.push(p)
							  
						  });
		                    
						  
		               },
		               error => {
		                   console.log('失败');
		               }
		           );
		  	 
			
		},
		getPackageNum(){
			 let url='/user/query/getPackageNum';
			 let data={
					userId:this.user.id
			    }
			 let entity={};
			 entity.url=url;
			 entity.data=data;
			 const that=this;
			 http.get(entity).then(
			           res => {
						 let t=res['0'];
						 res['0']=res['1'];
						 res['1']=t;
						 Object.assign(that.classNum,res)
						 
						  
			           },
			           error => {
			               console.log('失败');
			           }
			       );
			 
			
			
		}
		

	},
	mounted() {
		const that=this;
		uni.getSystemInfo({
		    success: function (res) {
				that.phoneStatus=res
				that.getPackageNum();
				that.getDataList()
				uni.startPullDownRefresh();
		    }
		});
		
		
		
	},
	onPullDownRefresh() {
	        const that=this;
	        setTimeout(function () {
				
				/* that.getPackageNum();
				that.getDataList() */
				
	            uni.stopPullDownRefresh();
	        }, 1000);
   }
	
}
